.guidance {
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  z-index: 100;
  background: #fff;
}

.guidance >.title {
  width: 10rem;
  height: 3.066666666666667rem;
  color: #429aff;
  display: -webkit-box;
  display: -webkit-flex;
  display: flex;
  -webkit-box-pack: center;
  -webkit-justify-content: center;
  justify-content: center;
  -webkit-box-align: center;
  -webkit-align-items: center;
  align-items: center;
  font-size: 0.613333333333333rem;
  margin-bottom: 1.2rem;
}

.guidance >.center {
  font-size: 0.4rem;
}

.guidance >.center .top {
  margin-bottom: 0.346666666666667rem;
}

.guidance >.center .company-box {
  position: relative;
}

.guidance >.center .company-box .company-shade {
  position: absolute;
  z-index: 50;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
}

.guidance >.center .bottom .bottom-group .weui-label {
  width: 3rem;
}

.guidance >.footer {
  height: 5.773333333333333rem;
  display: -webkit-box;
  display: -webkit-flex;
  display: flex;
  -webkit-box-pack: center;
  -webkit-justify-content: center;
  justify-content: center;
  -webkit-box-align: center;
  -webkit-align-items: center;
  align-items: center;
}

.guidance >.footer >.btn {
  width: 3.2rem;
  height: 1.066666666666667rem;
  background: #4799fe;
  color: #fff;
  display: -webkit-box;
  display: -webkit-flex;
  display: flex;
  -webkit-box-pack: center;
  -webkit-justify-content: center;
  justify-content: center;
  -webkit-box-align: center;
  -webkit-align-items: center;
  align-items: center;
  border-radius: 0.266666666666667rem;
}

.guidance .vux-cell-box {
  padding: 0.2rem 0;
  border-bottom: 0.026667rem solid #d9d9d9;
}

.guidance .vux-cell-box:before {
  border: none;
}

.guidance .vux-cell-box:nth-child(2) {
  border-top: none;
}

.guidance .vux-cell-box:nth-child(3) {
  border-top: none;
}

.guidance .inUp-enter-active,
.guidance .inUp-leave-active {
  -webkit-transition: all 0.3s;
  transition: all 0.3s;
}

.guidance .inUp-enter,
.guidance .inUp-leave-to {
  -webkit-transform: translate3d(0, 100%, 0);
  transform: translate3d(0, 100%, 0);
}


